
.row_dash {
    display: flex;
    margin-bottom: 10px;
}

.header {
    display: flex;
}
img.logo_taskmo {
    width: 150px;
    height: 35px;
    margin: 10px;
    margin-left: 130px;

}
.body {
    padding: 20px 150px;
    background: #E5E5E5;
}

.dashboard {
    padding: 30px 50px;
    text-align: start;
    background: #fff;
    box-shadow: 4px 4px 4px 1px rgb(0 0 0 / 8%);
    border-radius: 20px;
    margin-bottom: 30px;

}
.buttons {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
button.button_save.btn.btn-primary {
    margin-left: 20px;
    background: #673695;
    border: none;
    border-radius: 20px;
    padding: 10px 30px;
}
button.button_add.btn.btn-secondary {
    margin-left: 20px;
    background: #fff;
    border: 1px solid #673695;
    border-radius: 20px;
    padding: 10px 30px;
    color: #673695;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
p.taskmo_p1 {
    margin: 0;
    margin-left: 10px;
}
h1.form_header {
    display: flex;
    margin: 15px 0;
    color: #673695;

}
.switch_button {
    margin: 20px 0;
    display: flex;
    justify-content: flex-end;
}
button.text_button.btn.btn-primary {
    background: #f2f2f2;
    border: 1px solid #673695;
    color: #673695;
    width: 120px;
    border-radius: 1px;
    box-shadow: none;

}
button.file_button.btn.btn-primary {
    background: #673695;
    border: none;
    color: #fff;
    width: 120px;
    border-radius: 1px;
    box-shadow: none;

}

.add_button {
    display: flex;
    justify-content: flex-end;
}
